import React, { useEffect } from "react";

const Detail = (props) => {
  console.log(props);
  useEffect(() => {
    const { location, history } = props;
    if (location.state === undefined) {
      window.alert("잘못된 접근!");
      history.push("/");
    }
  });
  return <span>ㅎssㅇ</span>;
};

export default Detail;
